*{margin:0;padding:0}
.NV{
    display:flex;
    height:100vh;
    width:100vw;
}
.NV__L{
    flex-basis: 15%;
    position:relative;
    background-color: rgb(26,38,43);
    border:0;
    border-radius: 1.5px;
}
.NV__R{
    flex-basis:85%;
    display:flex;
    
    flex-direction: column;
}
.NV__RT{
    flex-basis:10%;
    display:flex;
    border:0;
    border-radius: 1.5px;
    position:relative;
    background-image: -webkit-linear-gradient(left,rgb(179,26,79) ,rgb(254,103,2));
}
.NV__RB{
    
   
}
/* SETTING CHUNG */
.NV__IMG{
    display:block;
    width:auto;
    height:20%;
    border:0px black;
    border-radius:50%;
    margin:8% auto;
}
.NV__L--text{
    font-family: Tahoma;
    color:rgb(154,159,162);
    text-align: center;
}
hr{
    border: solid #2c3e50;
    box-shadow:0px 0px 3px 1px #2c3e50;
    margin:5% 0;
}
.NV_L--heading{
    width:auto;
    height:10%;
    display:flex;
    align-items: center;
    font-family: Tahoma;
    color:rgb(154,159,162);
    padding-left:5%;
    justify-content: flex-start;
    border:0px;
    border-radius: 5%;
}
.NV_L--heading:hover{
   background-color: rgb(58,69,73);
   transition: 0.2s;
   padding-left:8%;
}
.NV_L--button{
   position:absolute;
   background-color: rgb(26,38,43);
   border:0px;
   color:white;
   width:50%;
   height:5%;
   bottom:1%;
   left:25%;  
}
/* KHOI BEN TRAI */

.NV__Home{
    color:white;
    text-align: center;
    font-size:30px;
    margin-left:2%;
    margin-top:1.5%;
}
.NV__Search{
    display:block;
    width:30%;
    height: 40%;
    margin: 1.8% 0 0 2%;
    border:0px;
    border-radius:10px;
    
}
.NV__Button{
    width:5%;
    height: 40%;
    margin: 1.8% 0 0 1%;
    border:0px;
    border-radius:10px;
    font-family:tahoma
}

.NV__RB--items{
    width:50%;
    height: 50%;
    justify-content: center;
    align-items: center;
    margin:10%;
    display:flex;
    flex-direction: column;
}
/* KHOI BEN PHAI */