.LogIn{
    width: 100vw;
    height:100vh;
    display:flex;
    justify-content: center;
    align-items: center;
    background-image: -webkit-linear-gradient(top right,rgb(116,114,120) 35%,rgb(97,98,112) 35%,rgb(93,95,110) 40%)
}

.LogInMain{
    height:40%;
    width:20%;
    background-color: #2c2c54;
    border: 1px solid #2c2c54;
    border-radius: 10%;
    box-shadow: 1px 10px 20px black;
}
.LogInForm{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.LogInText{
    margin:10% 0;
    color:whitesmoke;
    font-family: tahoma;
}
.LogIn_InPut{
    display:block;
    width:80%;
    padding:3%;
    margin-bottom: 5%;
    border:0px black;
    border-radius: 10px;
}
.LogInForm_Button{
    margin-top:5%;
    width:100%;
    display:flex;
    justify-content: center;
    font-family:segoe ui light;
    font-size:14px;
    color:whitesmoke;
}
.LogIn_Button{
    width:40%;
    height:30px;
    margin-left:5%;
    margin-top:-1%;
    border:0px black;
    border-radius: 10px;
}